footer,header,iframe,img,nav,section{
  display:block
}

.page-permalink.center-vert,.page-permalink.webkit-flex{
  -webkit-box-orient:vertical
}

*{
  margin:0;
  padding:0;
  box-sizing:border-box
}

audio,video{
  display:inline-block
}

audio:not([controls]){
  display:none;
  height:0
}

iframe{
  width:100%
}

img{
  max-width:100%;
  margin:0 auto
}

.has-google-fonts .head-title,.has-google-fonts .header-desc{
  font-family:Cormorant Garamond,sans-serif
}

img.avatar,ol.notes,svg.hidden{
  display:none
}

.post__footer li,.post__footer ul{
  display:inline
}

.metadata,.social{
  margin:.278em 0;
  line-height:19px;
  line-height:1.056
}

.like_button{
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  width:100%;
  height:100%;
  opacity:0;
  z-index:10
}

div.custom-like-button{
  position:relative
}

li.custom-like-button{
  display:inline-block
}

ol.notes{
  position:absolute;
  padding:0 0 50px;
  max-width:100%
}

body.has-center-text ol.notes{
  left:50%;
  transform:translateX(-50%);
  width:100%
}

.icon-heart,.like_text{
  opacity:.5
}

.like_button:hover+.like_text{
  opacity:1
}

.icon-heart,a:not(.nav-item){
  transition:all .2s
}

.post__footer .custom-like-button{
  position:relative
}

.post__footer .like_button:hover+.like_text{
  opacity:1
}

.post__footer .like_button.liked+.like_text{
  display:none
}

.post__footer .like_text--liked{
  display:none
}

.post__footer .like_button+.like_text+.like_text--liked{
  display:none;
  opacity:0
}

.post__footer .like_button.liked+.like_text+.like_text--liked{
  display:block;
  opacity:1
}

.icon-heart:hover{
  opacity:1
}

.tmblr-iframe.tmblr-iframe--sticky-ad{
  opacity:0
}

#infscr-loading{
  margin:auto;
  text-align:center
}

