/*tumblr system*/

.has-google-fonts {
  .head-title,
  .header-desc {
    font-family: Cormorant Garamond, sans-serif;
  }
}
img.avatar,
ol.notes,
svg.hidden {
  display: none;
}
.post__footer {
  li,
  ul {
    display: inline;
  }
}
.metadata,
.social {
  margin: 0.278em 0;
  line-height: 19px;
  line-height: 1.056;
}
.like_button {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 10;
}
div.custom-like-button {
  position: relative;
}
li.custom-like-button {
  display: inline-block;
}
ol.notes {
  position: absolute;
  padding: 0 0 50px;
  max-width: 100%;
}
body.has-center-text ol.notes {
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 100%;
}
.icon-heart,
.like_text {
  opacity: 0.5;
}
.like_button:hover + .like_text {
  opacity: 1;
}
.icon-heart,
a:not(.nav-item) {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.post__footer {
  .custom-like-button {
    position: relative;
  }
  .like_button {
    &:hover + .like_text {
      opacity: 1;
    }
    &.liked + .like_text {
      display: none;
    }
  }
  .like_text--liked {
    display: none;
  }
  .like_button {
    + .like_text + .like_text--liked {
      display: none;
      opacity: 0;
    }
    &.liked + .like_text + .like_text--liked {
      display: block;
      opacity: 1;
    }
  }
}
.icon-heart:hover {
  opacity: 1;
}
.tmblr-iframe.tmblr-iframe--sticky-ad {
  opacity: 0;
}
