.photoset {
  .container-l {
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: space-around;
  }
  img {
    flex-basis: 25%;
    width: 25%;
    flex-shrink: 2;
    flex-grow: 2;
    margin: 0 0 1em;

    &:last-child {
      display: block !important;
    }
  }
}
@keyframes wave {
  0% {
    transform: matrix(1, 0, 0, 0.7, 0, 10);
  }
  10% {
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
  30% {
    transform: matrix(1, 0, 0, 0.8, 0, -10);
  }
  100% {
    transform: matrix(1, 0, 0, 1, 0, -70);
  }
}

@keyframes sunk {
  0% {
    transform: translate(0, -3em);
    opacity: 0;
  }
  1% {
    transform: translate(0, -1em);
    opacity: 0.618;
  }
  100% {
    transform: translate(0, 0em);
    opacity: 1;
  }
}

@keyframes sunk-short {
  0% {
    transform: translate(0, -1em);
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  100% {
    transform: translate(0, 0);
    opacity: 1;
  }
}

/*index only*/

.index-img {
  height: 38vh;
  width: 100%;
  background-image: url(https://static.tumblr.com/y9oo5wl/ThPox0ei0/surface.gif);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: 120% auto;
  background-attachment: scroll;
  z-index: 1;
  transform: matrix(1, 0, 0, 0.9, 0, 20);
  animation: wave 180s 0s linear forwards;
}
.sunk {
  animation: sunk 30s 0.3s ease-out forwards;
  transform: translate(0px, -3em);
  opacity: 0;
}
.sunk-short {
  animation: sunk-short 3s 0.3s ease-out forwards;
  transform: translate(0px, -10em);
  opacity: 0;
}
